body {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.wrap-main {
  display: flex;
  height: 100%; }

.side-panel {
  width: 23.7%; }
  .side-panel .top-logo {
    height: 71px;
    background: #D1D4D9; }
    .side-panel .top-logo .logo-img {
      background: url(../img/logo_black.svg) no-repeat;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: 80%;
      display: inline-block; }
  .side-panel .navigation-city {
    background: #033953;
    padding: 26px 10px 45px 131px; }
    .side-panel .navigation-city .city {
      display: block;
      font-family: 'Gotham Pro';
      line-height: 46px;
      font-size: 1.7vw;
      font-weight: 300;
      color: #FFFFFF;
      text-decoration: none;
      position: relative;
      transition: color .5s; }
      .side-panel .navigation-city .city:hover {
        color: #DC143C; }
      .side-panel .navigation-city .city:first-child {
        margin-top: 23px; }
      .side-panel .navigation-city .city:first-child:before {
        content: "";
        position: absolute;
        width: 56.83px;
        height: 56.83px;
        background: url(../img/moscow.svg) no-repeat;
        left: -78px;
        top: -6px;
        background-size: 100%; }
      .side-panel .navigation-city .city:nth-child(2) {
        margin-top: 43px; }
      .side-panel .navigation-city .city:nth-child(2):before {
        content: "";
        position: absolute;
        width: 56.83px;
        height: 56.83px;
        background: url(../img/spb.svg) no-repeat;
        left: -77px;
        top: -6px;
        background-size: 100%; }
      .side-panel .navigation-city .city:last-child {
        margin-top: 42px; }
      .side-panel .navigation-city .city:last-child:before {
        content: "";
        position: absolute;
        width: 56.83px;
        height: 56.83px;
        background: url(../img/saratov.svg) no-repeat;
        left: -78px;
        top: -3px;
        background-size: 100%; }

.content-sidebar {
